.quote {
    @apply text-xl md:text-4xl font-semibold mx-auto py-5 pb-20 text-center text-white;
}

.orange-text {
    @apply bg-gradient-to-b from-[#FF512F] to-[#F09819] text-transparent bg-clip-text font-bold;
}

.light-orange-text {
    @apply bg-gradient-to-b from-[#E65C00] to-[#F9D423] text-transparent bg-clip-text font-bold;
}
